.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 18px;
  color: #272727;
}

.bannerImage {
  width: 100vw;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.navbar {
  box-shadow: 0px 1px 1px 0px rgb(0 0 0 / 3%);
  -webkit-box-shadow: 0px 1px 1px 0px rgb(0 0 0 / 3%);
}


.letter-preview {
  width:50%;
}

.roadmap {
  width: 400px;
  margin-top:-60px;
  z-index:51
}


.roadmap-header{
  z-index: 52;
}

h3 {
  font-weight: 600 !important;
  text-transform: uppercase;
  color: #b88f7a;
}


.rm-left,.rm-left-first {
  border-bottom: 3px solid #454849;
  min-height: 100px;
  position:relative;

}
.rm-left {
  border-right:3px solid #454849;

}

.rm-right {
  min-height: 100px;
  border-bottom: 3px solid #454849;
  border-left: 3px solid #454849;
  margin-left: -3px;
  position:relative;
}

.circle {
  border-radius: 50%;
  height:40px;
  width:40px;
  text-align: center;
  font-weight: 600;
  padding-top:7px;
  font-size: 0.7em;
  background: #454849;
  color: white;
  padding-left: 3px;
}

.rm-left > .circle, .rm-left-first  > .circle{
  position: absolute;
  bottom: -20px;
  left: -50px;
}

.rm-right > .circle{
  position: absolute;
  bottom: -20px;
  right: -50px;
}

.roadmap-container {
  text-align: left;

  line-height: 1.4em;
}

.roadmap-container b {
  color:#855147;
}

.roadmap-container img {
  margin-top: -30%;
}

.roadmap-container p {
  padding:10px;
}
